<template>
    <div class="Rand">
        <Randtitle></Randtitle>
        <Randcontent @add="addfn" :nowdata="nowdata" @give="givefn" :AnswerGroups="AnswerGroups"
            :Driveanswer="Driveanswer"></Randcontent>
        <Randfooter @toggleStar="toggleStarfn" :starActive="starActive" :AnswerGroups="AnswerGroups"></Randfooter>

    </div>
</template>

<script>
    import Randtitle from './Randtitle'
    import Randfooter from './Randfooter'
    import driveanswer from '../../../public/driveanswer'
    import Randcontent from './Randcontent'
    export default {
        name: 'Rand',
        components: {
            Randtitle,
            Randfooter,
            Randcontent
        },
        data() {
            return {
                Driveanswer: driveanswer,
                starActive: false,
                choseActive: false,
                AnswerGroups: {
                    Answertrue: 0,
                    Answerfalse: 0,
                    Answerid: null,
                    Answer: null,
                    Answerno: 0,
                    AnswerAllnum: null,
                    AnswerRandarr: []
                },
                nowdata: {
                    data: null,
                    nowId: '',
                    nowurl: '',
                    nowquestion: '',
                    answer: '',
                    expalins: '',
                    chose: '',
                    nowlength: []
                }
            }
        },
        watch: {
            'nowdata.data'() {
                //重置默认状态
                this.nowdata.nowlength = []
                let data = this.nowdata.data
                this.nowdata.nowId = data.id
                this.nowdata.nowquestion = data.question
                this.nowdata.answer = data.answer
                this.nowdata.nowurl = data.url
                this.nowdata.expalins = data.expalins
                for (let key in data) {
                    if (key.match('item')) {
                        this.nowdata.nowlength.push(data[key])
                    }
                }
                console.log(this.nowdata.nowlength)

                // 每次清除重复数组
                if (Array.from(new Set(window.localStorage.CollectionQuestion.split(','))).indexOf(this.nowdata.nowId
                        .toString()) != -1) {
                    this.starActive = true
                } else {
                    this.starActive = false
                }
                this.choseActive = false
            },
            "starActive"() {
                //获取本地数据
                let Collection = Array.from(new Set(window.localStorage.CollectionQuestion.split(',')))
                //判断是否为空数组
                if (Collection[0] == '') {
                    Collection.splice(0, 1)
                }
                if (this.starActive) {
                    //添加数组
                    Collection.push((this.nowdata.nowId).toString())
                    console.log('收藏')
                    this.choseActive = true
                } else {
                    console.log('取消收藏')
                    //删除数组
                    console.log(this.nowdata.nowId.toString())
                    if (this.choseActive) {
                        Collection.splice(Collection.indexOf(this.nowdata.nowId.toString()), 1)
                    }
                }
                //保存到数据库
                window.localStorage.CollectionQuestion = Array.from(new Set(Collection)).toString()
            },
        },
        methods: {
            toggleStarfn() {
                this.starActive = !this.starActive
                console.log('xxx')
            },
            givefn(str) {
                this.nowdata.data = str
            },
            addfn(keyname) {
                this.AnswerGroups[keyname]++
            }
        },
        created() {
            //初始化随机生成题目数量
            this.AnswerGroups.AnswerAllnum = this.Driveanswer.result.length
            while (this.AnswerGroups.AnswerRandarr.length != this.AnswerGroups.AnswerAllnum) {
                let rand = Math.floor(Math.random() * this.AnswerGroups.AnswerAllnum)
                if (this.AnswerGroups.AnswerRandarr.indexOf(rand) === -1) {
                    this.AnswerGroups.AnswerRandarr.push(rand)
                }
            }
            // console.log(this.AnswerGroups.AnswerRandarr)
        },
    }
</script>