<template>
    <div class="Randcontent">
        <div class="content">
            <div class="test-title">
                <div class="test-icon">
                    <span v-if="nowdata.nowlength===4">选择题</span>
                    <span v-else>判断题</span>
                </div>
                <span class="test-question">
                    {{nowdata.nowquestion}}
                </span>
            </div>
            <div v-if="nowdata.nowurl" class="test-img">
                <img :src="nowdata.nowurl">
            </div>
            <div class="answerb">
                <li v-for="(item, index) in nowdata.nowlength" :key="index">
                    <div v-if="item" class="option">
                        <span :class="[errornum===index+1?'error':'']" @click="checkanswer(index+1)"
                            class="option-item">{{indexAz[index+1]}}</span>
                        <span class="chose-question">{{item}}</span>
                    </div>
                </li>
            </div>

            <div v-if="alertErrorActive" style="margin-top:3rem" class="alertError">
                <hr>

                <h1>正确答案:{{indexAz[nowdata.answer]}}</h1>
                <p
                    style="line-height:2.5rem;word-break:break-all;font-size:1.5rem;letter-spacing:0.3rem;text-indent:2rem">
                    {{nowdata.expalins}}</p>
                <button @click="btnnext"
                    style="position:fixed;top:50%;right:0%;padding:0.5rem 3rem;border:none;border-radius:2rem;font-weight:bold;background:green;color:white;">下一题</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Randcontent',
        props: ['AnswerGroups', 'Driveanswer', 'nowdata'],
        data() {
            return {
                alertErrorActive: false,
                errornum: null,
                starActive:false,
                indexAz: {
                    1: 'A',
                    2: 'B',
                    3: 'C',
                    4: 'D'
                },

            }
        },
        created() {
            //初始化数据
            let x = this.Driveanswer.result[this.AnswerGroups.AnswerRandarr[this.AnswerGroups.Answerno]]
            this.$emit('give', x)

        },
        methods: {
            

            btnnext() {
                this.errornum = null
                this.alertErrorActive = false
                let x = this.Driveanswer.result[this.AnswerGroups.AnswerRandarr[this.AnswerGroups.Answerno]]
                this.$emit('give', x)
                //当题目完成时跳转页面
            },
            checkanswer(index) {
                if (this.alertErrorActive) {
                    return
                }
                if (index == this.nowdata.answer) {
                    console.log('答题正确')
                    this.$emit('add', 'Answertrue')
                    this.$emit('add', 'Answerno')
                    let x = this.Driveanswer.result[this.AnswerGroups.AnswerRandarr[this.AnswerGroups.Answerno]]
                    this.$emit('give', x)
                } else {
                    this.errornum = index
                    this.alertErrorActive = true
                    console.log('答题错误')
                    this.$emit('add', 'Answerfalse')
                    this.$emit('add', 'Answerno')
                    //获取本地储存
                    let locationinfo = window.localStorage.ErrorBook
                    //将获取到的信息用数组保存
                    let newLocationinfo = []
                    for (let index = 0; index < locationinfo.split(',').length; index++) {
                        //删除空数组
                        if (locationinfo.split(',')[index] === '') {
                            continue;
                        }
                        //删除重复数组
                        if (newLocationinfo.indexOf(locationinfo.split(',')[index]) === -1) {
                            newLocationinfo.push(locationinfo.split(',')[index])
                        }
                    }
                    //获取错误题目的ID
                    let errorId = this.nowdata.nowId
                    //获取新生成的错误题目ID
                    newLocationinfo.push(errorId)
                    //错误题目总数提交到本地储存
                    window.localStorage.ErrorNum = newLocationinfo.length
                    //提交新生成的错误ID并且保存到本地存储
                    window.localStorage.ErrorBook = newLocationinfo.toString()
                    console.log(errorId)
                    console.log(newLocationinfo)
                    return
                }
                //当题目完成时跳转页面

            }
        },
    }
</script>


<style lang="less">
    .Randcontent {
        margin-bottom: 5rem;
        display: flex;
        flex-wrap: wrap;

        .content {
            padding: 2rem;
            width: 100%;

            .test-title {

                font-size: 1.8rem;

                .test-icon {
                    display: inline-block;
                    font-size: 1.2rem;
                    background: yellowgreen;
                    border-top-left-radius: 20rem;
                    border-bottom-left-radius: 20rem;
                    border-top-right-radius: 20rem;
                    padding: 0.5rem 1rem;
                    margin-right: 1rem;

                    span {
                        color: white;
                    }
                }
            }

            .test-img {
                width: 100%;
                display: flex;
                justify-content: center;
                margin: 2rem 0;

                img {
                    max-width: 100%;
                }
            }

            .answerb {
                display: flex;
                flex-wrap: wrap;
                margin-top: 3rem;

                li {
                    width: 100%;
                    margin: 1rem 0;

                    .option {
                        font-size: 2rem;

                        .option-item {
                            &.error {
                                background: red;
                            }

                            &.success {
                                background: green;
                            }

                            padding: 0.5rem;
                            border-radius: 50%;
                            box-shadow: 0 0 0.5rem 0.2rem lightgray;
                            cursor: pointer;
                        }

                        .chose-question {
                            line-height: 2;
                            margin-left: 2rem;
                        }
                    }
                }
            }

        }
    }
</style>